.settings-toggle_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    position: relative;
    margin: .2vw;
    padding: .3vw;
    padding-left: 10px;
    padding-right: 10px;
    text-align: left;

    background: rgba(0, 0, 0, 0.35);
    border: 0.5px solid rgba(255, 255, 255, 0.75);
    color: rgba(255, 255, 255, 0.89);

    transition: 200ms ease-out ease-in;
}

.settings-toggle_toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;

    width: 13px;
    height: 13px;

    background: rgba(0, 0, 0, 0.35);
    border: 0.5px solid rgba(255, 255, 255, 0.75);

    -webkit-user-select: none;

            user-select: none;
    cursor: pointer;
}

.settings-toggle_fill {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.settings-modal_overlay {
    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed; 
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.8);
}

.settings-modal_content {
    width: 30vw;
}

.settings-modal_overlay p {
    margin-bottom: 10px;
    text-align: left;
    color: white;
    font-size: 3vw;
}

.settings-modal_overlay input {
    width: 100%;
    background-color: transparent;
    border: none;
    color: white;
    border-bottom: 1px solid white;
    font-size: 3vw;
}

.settings-modal_overlay input:focus {
    outline:none;
}

.settings-modal_overlay button {
    padding: 5px;
    width: 100%;
    color: white;
    font-size: 2vw;
    border: none;
    margin-top: 10px;
    background: rgba(0,0,0,0.5); /* Black w/ opacity */
    cursor: pointer;
    border: .5px solid white;
    transition: 200ms;
}

.settings-modal_overlay button:hover {
    background: rgba(255,255,255,0.5); /* Black w/ opacity */
    color: black;
}

.settings-modal_overlay button:focus {
    outline:none;
}

.settings-modal_button-tray {
    width: 100%;
    display: flex;
    flex-direction: row;
}
.knob_wrapper {
    position: absolute;
    border-radius: 50%;
    background-color: transparent;
}

.knob_bg-image {
    max-width: 100%;
    max-height: 100%;
    border-style: none;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: perspective(1px) translateZ(0);
    -webkit-font-smoothing: subpixel-antialiased;
}

.knob_highlighter {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color:rgba(0,200,0,0.2);
    pointer-events: none;

    opacity: 0;
    transition: 400ms;
}
.button_wrapper {
    position: absolute;  
    width: 6.66%;
    height: 5.55%;
    top: 53.5%;
    background-color: transparent;

    transition: 50ms;
}

.button_tint-overlay {
    position: absolute;
    border-radius: 3px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    background-blend-mode: multiply;
}

.button_tint-overlay:active {
    background: rgba(255, 0, 0, 0.2);;
    background-blend-mode: multiply;
}

.button_wrapper:hover {
    transform: scale(.95);
}

.button_bg-image {
    max-width: 100%;
