}

.key_light-overlay {
    position: absolute;
    width: 75%;
    left: 5px;
    top: -3px;
    opacity: 0;
    animation: flicker .1s infinite; 
    visibility: hidden;
}

@keyframes flicker {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


.light-toggle_wrapper {
    position: absolute;  
    width: 3.333%;
